import Vue from 'vue'
import Router from 'vue-router'
// import axios from 'axios'

// import store from '../store'
// import HelloWorld from '@/components/HelloWorld'
import index from '../pages/index.vue'
import printInfo from '../pages/printInfo.vue'

import printLicense from '../pages/printLicense'
import printLicenseOne from '../pages/printLicense/one'
import printLicenseTwo from '../pages/printLicense/two'
import printLicenseThree from '../pages/printLicense/three'
import printLicenseFour from '../pages/printLicense/four'

import printStatusNoCompany from '../pages/printStatus/print-no-company'
import printStatusNoLicense from '../pages/printStatus/print-no-license'
import printStatusSuccess from '../pages/printStatus/print-success'
import printStatusFail from '../pages/printStatus/print-fail'



Vue.use(Router)


const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/printLicense',
      name: 'printLicense',
      component: printLicense,
      children: [
        {path: '/printLicense/one', component: printLicenseOne, name: '', menuShow: true},
        {path: '/printLicense/two', component: printLicenseTwo, name: '', menuShow: true},
        // 
        {path: '/printLicense/three', component: printLicenseThree, name: '', menuShow: true},
        // 
        {path: '/printLicense/four', component: printLicenseFour, name: '', menuShow: true},
        // 
        {path: '/printLicense/print-no-company', component: printStatusNoCompany, name: '', menuShow: true},
        // 
        {path: '/printLicense/print-no-license', component: printStatusNoLicense, name: '', menuShow: true},
        {path: '/printLicense/print-success/:canPrint', component: printStatusSuccess, name: '', menuShow: true, props: true},
        {path: '/printLicense/print-success', component: printStatusSuccess, name: '', menuShow: true},
        {path: '/printLicense/print-fail', component: printStatusFail, name: '', menuShow: true}
      ]
    },
    {
      path: '/printInfo',
      name: 'printInfo',
      component: printInfo
    },
    {
      path: '/recognition',
      name: 'printInfo',
      component: printInfo
    }
  ]
})

// router.beforeEach((to, from, next) => {
//  // params
//  axios.get('common/errors').then(data => {
//     if(data.status == 200){
//         window.__common_errors = data.data;
//     }
// })
//  next() // :-D
// })


export default router



